<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'设备状态码'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-resource-status-code
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-resource-status-code>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditResourceStatusCode from "./create-or-edit-resource-status-code/create-or-edit-resource-status-code.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "DataV2ResourceStatusCode",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditResourceStatusCode,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该状态码吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const categories = ["硬件故障", "软件故障", "网络故障", "系统故障", "其他"];
        const severities = ["严重", "重要", "次要", "警告", "提示"];
        const statuses = ["启用", "禁用"];
        
        for (let i = 0; i < 25; i++) {
          dataList[i] = {
            id: i + 1,
            statusCode: `${String.fromCharCode(69 + i % 5)}${String(i + 1).padStart(3, '0')}`,
            name: `${categories[i % 5]}状态${i + 1}`,
            category: categories[i % 5],
            severity: severities[i % 5],
            description: `这是一个${categories[i % 5]}相关的状态码，用于表示${severities[i % 5]}级别的问题`,
            solution: `建议采取以下措施：\n1. 检查相关设备\n2. 进行必要维护\n3. 联系技术支持`,
            status: statuses[i % 2],
            createdBy: "admin",
            createdTime: "2024-03-18 " + (10 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.statusCode.indexOf(this.filterText) > -1 || 
                   item.statusName.indexOf(this.filterText) > -1 ||
                   item.category.indexOf(this.filterText) > -1 ||
                   item.severity.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1 ||
                   item.solution.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style> 